h1 {
    font-family: Railway;
    font-size: 3rem;
}

h1, li, a {
    color: #b94747;
}


.header {
    position: fixed;
    width: 100%;
    height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3rem;
    z-index: 10;
}

.header.scrolled {
    background: #ffffffb5; /* The background you want when scrolled */
  }

.nav-menu {
    display: flex;
}

.nav-menu li {
    padding: 0 1rem;
}
/* --------------------------- */



.nav-menu li a:hover {
    color:#cc4e4e;
}

/* --------------------------- */
.nav-menu a {
    font-size: 1.2rem;
    font-weight: 500;
}

.hamburger {
    display: none;
}

@media screen and (max-width:1240px) {
    .hamburger {
        display: block;

    }

    .nav-menu {
        position: absolute;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        text-align: center;
        width: 100%;
        height: 100vh;
        transition: 0.3s;
        z-index: -1;
        background: rgba(0, 0, 0, 0.872)
    }

    .nav-menu.active {
        left:0;
    }

    .nav-menu li {
        padding: 1rem 0;
    }

    .nav-menu a {
        font-size: 2rem;
    }
}
